export default {
    props:{           
        item:Object,
        index:Number           
    },
    data() {
        return {
            isFollow: this.item.isFollow
        }
    },
    methods:{
        follow(){
            this.isFollow = !this.isFollow;
            if(!this.isFollow){
                uni.showToast({
                    title:"取消关注成功",
                })
            }
            else{
                uni.showToast({
                    title:"关注成功",
                })
            }
        }
    }
}
